:root {
    --icon-font-family: "Material Icons";
}

@font-face {
    font-family: "Material Icons";
    font-style: normal;
    font-weight: 400;
    src: local("Material Icons"),
        local("MaterialIcons-Regular"),
        url("../font/materialicons/v103/MaterialIcons-Regular.ttf") format("truetype"),
        url("../font/materialicons/v103/MaterialIcons-Regular.woff2") format("woff2");
}

@media all {
    .material-icons {
        direction: ltr;
        display: inline-block;
        font-family: var(--icon-font-family);
        font-size: 24px; /* Preferred icon size */
        font-style: normal;
        font-weight: normal;
        letter-spacing: normal;
        line-height: 1;
        text-transform: none;
        white-space: nowrap;
        word-wrap: normal;
    }
}

@media all {
    .aa-sovtimer-timer-elapsed {
        color: rgb(255 0 0);
    }

    .aa-sov-timer-zkb-icon {
        margin-left: 1rem;
    }

    .aa-sovtimer-active-campaign {
        border-left: 1px solid rgb(255 0 0);
        border-right: 1px solid rgb(255 0 0);
    }

    .aa-sovtimer-active-campaign td {
        background-color: rgb(255 0 0 / 12.5%);
    }

    .aa-sovtimer-upcoming-campaign {
        border-left: 1px solid rgb(255 165 0);
        border-right: 1px solid rgb(255 165 0);
    }

    .aa-sovtimer-upcoming-campaign td {
        background-color: rgb(255 165 0 / 12.5%);
    }

    .aa-sovtimer-trend {
        cursor: default;
        display: inline-block;
        left: 0.5rem;
        margin-right: 1rem;
        position: relative;
        top: 0.75rem;
    }

    .aa-sovtimer-trend-up {
        color: rgb(76 175 80);
    }

    .aa-sovtimer-trend-down {
        color: rgb(255 0 0);
    }

    .aa-sovtimer-campaigns-progress-column {
        width: 100px !important;
    }

    .aa-sovtimer-entity-logo-left {
        margin-right: 0.5rem;
    }

    .aa-sovtimer .aa-sovtimer-campaigns td {
        vertical-align: middle;
    }

    .aa-sovtimer-footer {
        border-top: 1px solid rgb(236 240 241);
        font-size: 85%;
        margin-top: 1rem;
        padding-top: 1rem;
    }

    .template-dark-mode .aa-sovtimer-footer {
        border-color: rgb(70 69 69);
    }
}

/* DataTables
--------------------------------------------------------------------------------------------------------------------- */
@media all {
    .dataTables_wrapper > .form-inline {
        margin-bottom: 1rem;
    }

    .dataTables_wrapper > .form-inline select {
        margin-bottom: 1rem;
        margin-right: 1rem;
    }

    .dataTables_wrapper > .form-inline select:last-child {
        margin-right: 0;
    }

    .dataTables_length select {
        margin: 0 1rem;
    }
}

@media (min-width: 768px) {
    .dataTables_wrapper > .form-inline select:first-child {
        margin-left: 1rem;
    }
}
